.App{
  font-family: 'Sono', sans-serif;
 /* background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);*/
  color:black
}
.App{
  text-align: center;
}
h1{
  text-align: center;
  margin-top:3%;
}
button{
  border:none;
  height:7vh;
  background-image: linear-gradient(-20deg, #fc6076 0%, #ff9a44 100%);
  border-radius:7px;
  font-size: medium;
  margin-top:10%;
  font-weight: 400;
}
button:hover{
  background-image: linear-gradient(to right, #f9d423 0%, #ff4e50 100%);
  
}
.Quote{
  border:2px solid grey;
  padding:6% 4% 4% 4%;
  border-radius:5px;
  height:30vh;
  text-align: center;
  color:darkblue;
  font-weight:600;
  font-size:20px;
}
.RandomQuote{
  padding:4% 30% 0% 30%;
}
strong{
  display: block;
  text-align: right;
  padding:5% 5% 6% 5%;
  color:black;
  font-size:15px;
}